.para-layer {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0%; }

.para-absolute {
  position: absolute;
  width: 50%;
  margin-left: 25%; }

.spotlight {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0; }

.teddybear {
  position: absolute;
  width: 15%;
  height: auto;
  left: 42%;
  margin-top: 47%; }

.spooky-neo {
  position: absolute;
  width: 100%; }

.flicker {
  -webkit-animation: flicker 6s alternate linear infinite;
  animation: flicker 6s alternate linear infinite; }

.flicker2 {
  -webkit-animation: flicker2 6s alternate linear infinite;
  animation: flicker2 6s alternate linear infinite; }

@keyframes flicker {
  0%, 19.999%, 22%, 54.999%, 56%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: .99;
    text-shadow: -1px #000; }
  20%, 21.999%, 55%, 55.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: 0;
    text-shadow: none; } }
@-webkit-keyframes flicker {
  0%, 19.999%, 22%, 54.999%, 56%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: .99;
    text-shadow: -1px #000; }
  20%, 21.999%, 55%, 55.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: 0;
    text-shadow: none; } }
@keyframes flicker2 {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: 0;
    text-shadow: none;
    -webkit-transform: scale(1.8);
            transform: scale(1.8); }
  20%, 21.999% {
    opacity: .99;
    text-shadow: -1px #000;
    -webkit-transform: scale(1);
            transform: scale(1); }
  63%, 63.999%, 69%, 69.999% {
    opacity: .99;
    text-shadow: -1px #000; } }
@-webkit-keyframes flicker2 {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: 0;
    text-shadow: none;
    -webkit-transform: scale(1.8);
            transform: scale(1.8); }
  20%, 21.999% {
    opacity: .99;
    text-shadow: -1px #000;
    -webkit-transform: scale(1);
            transform: scale(1); }
  63%, 63.999%, 69%, 69.999% {
    opacity: .99;
    text-shadow: -1px #000; } }



