:root {
  --cursorX: 50vw;
  --cursorY: 50vh;
  --animation-time: 5s;
  --lights: radial-gradient( circle 25vmax at var(--cursorX) var(--cursorY), rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0.35) 90%, rgba(0, 0, 0, 0.99) 100%); }

:root:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  pointer-events: none;
  background: var(--lights);
  z-index: 3; }

.white-font {
  color: snow; }

.flip-up {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg); }

.light-switch {
  position: fixed;
  height: 100px;
  width: auto;
  top: 1%;
  right: 0%; }

.light-text {
  position: absolute;
  left: 47%;
  bottom: 7%; }

#title-glitch3 {
  -webkit-animation: glitchLayer 1s linear alternate infinite;
  animation: glitchLayer 1s linear alternate infinite; }

#title-glitch4 {
  -webkit-animation: glitchLayer2 2s ease alternate infinite;
  animation: glitchLayer2 2s ease alternate infinite; }

.no-access {
  opacity: 0; }

.yes-access {
  opacity: 1; }

.no-allow {
  opacity: 0;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0); }

.yes-allow {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1); }

@-webkit-keyframes glitchLayer {
  0%, 36.5% {
    opacity: 0; }
  38%, 67.5% {
    opacity: 0.4;
    -webkit-transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0);
            transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0); }
  72% {
    opacity: 0.4;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-transform: translate3d(-20px, -20px, 0px);
            transform: translate3d(-20px, -20px, 0px); }
  39.5%, 66%, 69%, 73.5%, 100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px); } }
@keyframes glitchLayer {
  0%, 36.5% {
    opacity: 0; }
  38%, 67.5% {
    opacity: 0.4;
    -webkit-transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0);
            transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0); }
  72% {
    opacity: 0.4;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-transform: translate3d(-20px, -20px, 0px);
            transform: translate3d(-20px, -20px, 0px); }
  39.5%, 66%, 69%, 73.5%, 100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px); } }
@-webkit-keyframes glitchLayer2 {
  0%, 36.5% {
    opacity: 0; }
  38%, 67.5% {
    opacity: 0.4;
    -webkit-transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0);
            transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0); }
  72% {
    opacity: 0.4;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-transform: translate3d(-20px, -20px, 0px);
            transform: translate3d(-20px, -20px, 0px); }
  39.5%, 66%, 69%, 73.5%, 100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px); } }
@keyframes glitchLayer2 {
  0%, 36.5% {
    opacity: 0; }
  38%, 67.5% {
    opacity: 0.4;
    -webkit-transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0);
            transform: translate3d(var(--gap-horizontal), var(--gap-vertical), 0); }
  72% {
    opacity: 0.4;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-transform: translate3d(40px, 40px, 0px);
            transform: translate3d(40px, 40px, 0px); }
  39.5%, 66%, 69%, 73.5%, 100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px); } }
@media screen and (max-width: 1200px) {
  .light-switch {
    height: 50px; }

  .light-text {
    bottom: 30%;
    left: 44%; } }



